/* Style de la scroll bar pour les thèmes clairs */
@media (prefers-color-scheme: light) {
    /* Scrollbar container */
    ::-webkit-scrollbar {
      width: 12px; /* Largeur de la barre de défilement */
    }

    /* Track (fond de la scroll bar) */
    ::-webkit-scrollbar-track {
      background-color: #f0f0f0; /* Couleur du fond */
    }

    /* Thumb (la partie glissable de la scroll bar) */
    ::-webkit-scrollbar-thumb {
      background-color: #b0b0b0; /* Couleur de la barre */
      border-radius: 10px; /* Bordures arrondies */
      border: 3px solid #f0f0f0; /* Espace autour de la barre */
    }

    /* Hover state pour la scroll bar */
    ::-webkit-scrollbar-thumb:hover {
      background-color: #888; /* Couleur lors du survol */
    }
}

/* Style de la scroll bar pour les thèmes sombres */
@media (prefers-color-scheme: dark) {
    /* Scrollbar container */
    ::-webkit-scrollbar {
      width: 12px;
    }

    /* Track (fond de la scroll bar) */
    ::-webkit-scrollbar-track {
      background-color: #2b2b2b; /* Fond plus sombre */
    }

    /* Thumb (la partie glissable de la scroll bar) */
    ::-webkit-scrollbar-thumb {
      background-color: #505050; /* Couleur de la barre dans un thème sombre */
      border-radius: 10px;
      border: 3px solid #2b2b2b; /* Espace autour de la barre */
    }

    /* Hover state pour la scroll bar */
    ::-webkit-scrollbar-thumb:hover {
      background-color: #666; /* Couleur lors du survol */
    }
}

